@charset "UTF-8";
.p-body {
  background-image: url(../img/22116996_m.jpg);
  background-size: cover;
}

.p-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 80;
  width: 100%;
  height: 60px;
  font-family: "Architects Daughter", cursive;
  transition: all 0.2s;
  overflow: hidden;
}

.p-header__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 35px;
  margin-right: 40px;
}

.p-header__nav__block {
  margin-top: -10px;
  width: 40%;
  height: 25px;
}

.p-header__nav {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-right: 35%;
}

.p-header__nav__item_label {
  padding: 10px 15px;
  text-decoration: none;
  color: aliceblue;
  font-size: 25px;
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
}

.p-footer {
  width: 100%;
  height: 60px;
  padding-top: 2.5%;
  background-color: #ffc9d2;
  overflow: hidden;
}

.p-footer__menu {
  text-align: center;
}

.p-footer__menu__copy {
  font-family: "Architects Daughter", cursive;
  color: gray;
  font-weight: bold;
}

.p-hero {
  position: relative;
  z-index: 40;
  width: 100%;
  height: 0;
  margin-bottom: 300px;
  padding-bottom: 46.25%;
  overflow-y: hidden;
}

.p-hero__label {
  position: absolute;
  z-index: 120;
  bottom: 50px;
  right: 10%;
  font-size: 30px;
  font-family: "Architects Daughter", cursive;
  color: white;
}

.p-hero__stage_drapes_1__png {
  position: absolute;
  z-index: 60;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.black {
  position: absolute;
  z-index: 70;
  width: 100%;
  height: 0;
  padding-bottom: 75%;
  background-color: #161616;
  opacity: 0.5;
  overflow: hidden;
}

#wrapper {
  position: absolute;
  z-index: 200;
  width: 100%;
  height: 100%;
}

#effect {
  position: absolute;
  z-index: 200;
  width: 100%;
  height: 300%;
}

#curtain1 {
  position: fixed;
  z-index: 200;
  top: 0px;
  left: 0px;
  width: 50%;
  height: 300%;
  transition: all 1.5s;
}

#curtain2 {
  position: fixed;
  z-index: 200;
  top: 0px;
  right: 0;
  width: 50%;
  height: 250%;
  transition: all 1.5s;
}

#wrapper_c2 {
  position: relative;
  z-index: 200;
  width: 100%;
  height: auto;
}

#effect_c2 {
  position: absolute;
  z-index: 200;
  left: 50%;
  transform: translate(-50%, -50%);
  /*真ん中に配置*/
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  width: 100%;
  height: auto;
}

#curtain1_c2 {
  position: absolute;
  z-index: 200;
  top: 0px;
  left: 0px;
  width: 50%;
  transition: all 1.5s;
}

#curtain2_c2 {
  position: absolute;
  z-index: 200;
  top: 0px;
  right: 0;
  width: 50%;
  transition: all 1.5s;
}

.box__wrapper {
  margin-top: 500px;
  margin-bottom: 500px;
}

.box {
  position: absolute;
  top: 40%;
  left: 5%;
}

.skill {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  /*真ん中に配置*/
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  width: 80%;
  object-fit: contain;
}

.p-button__wrapper {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  margin-right: 5%;
  margin-bottom: 60px;
  transition: all 0.2s;
}

.p-button {
  width: 20%;
  height: 35px;
  padding: 5px 10px;
  background-color: #e2f020;
  border-radius: 65px;
  border: none;
  transition: all 0.2s;
}

.c-btn-label {
  font-size: 20px;
  color: aliceblue;
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
}

.p-content {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 62.5%;
  background-image: url(../img/22116996_m.jpg);
  background-size: cover;
  overflow-y: hidden;
}

.p-content__stage_drapes_1__png {
  position: absolute;
  z-index: 100;
  top: 300px;
  left: 50%;
  transform: translate(-50%, -50%);
  /*真ん中に配置*/
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  object-fit: cover;
  object-position: center;
}

.p-content__stage_drapes_2__png {
  position: absolute;
  z-index: 200;
  top: 1000px;
  left: 50%;
  transform: translate(-50%, -50%);
  /*真ん中に配置*/
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  margin-bottom: 1600px;
  object-fit: cover;
  object-position: center;
}

.p-content_2 {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 20%;
  padding-bottom: 80%;
  background-color: #cecccc;
  background-image: linear-gradient(335deg, #493626 23px, transparent 23px), linear-gradient(155deg, #553f2e 23px, transparent 23px), linear-gradient(335deg, #493626 23px, transparent 23px), linear-gradient(155deg, #553f2e 23px, transparent 23px);
  background-size: 58px 58px;
  background-position: 0px 2px, 4px 35px, 29px 31px, 34px 6px;
  overflow-y: hidden;
}

